.container {
  display: grid;
  grid-template-columns: 268px 1fr 268px;
  grid-template-rows: auto 1fr auto;
  grid-column-gap: 32px;
  min-height: 100vh;
  max-width: 1270px;
  margin: 0 auto;
}

header {
  height: 80px;
  grid-column: span 3;
  text-align: center;
  font-size: 1.4em;
  background-color: #928d8d;
  -webkit-box-shadow: 0 4px 8px 0 rgba(51, 33, 34, 0.3);
          box-shadow: 0 4px 8px 0 rgba(51, 33, 34, 0.3);
  display: grid;
  grid-template-columns: 268px 1fr 268px;
  grid-column-gap: 32px;
  z-index: 10;
}

header .Logo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  grid-column: span 1;
  -ms-flex-pack: center;
      justify-content: center;
  opacity: 0.5;
  font-size: 37px;
  font-weight: bold;
  line-height: 0.57;
  color: #ebebe6;
}

header .Menu {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-pack: justify;
      justify-content: space-between;
  grid-column: span 2;
  padding-right: 10px;
}

header .Menu__mobile {
  display: none;
}

header .Menu a {
  font-size: 18px;
  font-weight: bold;
  line-height: 1.17;
  text-decoration: none;
  color: white;
  padding-bottom: 16px;
}

header .Menu a.active {
  border-bottom: 5px solid #9d0b0e;
  padding: 11px 0;
}

main {
  -ms-flex: 1 1;
      flex: 1 1;
}
 
aside {
  background-color: #ebebe6;
  -webkit-box-shadow: 0 4px 8px 0 rgba(51, 33, 34, 0.3);
          box-shadow: 0 4px 8px 0 rgba(51, 33, 34, 0.3);
}

aside .AsideTitle {
  margin: 25px 16px 16px;
  font-weight: bold;
  font-size: 18px;
  line-height: 1.17;
  color: #928d8d;

}

footer {
  grid-column: span 3;
  background-color: #690;
  color: white;
  height: 105px;
  background-color: #928d8d;
}

h1 {
  margin-bottom: 1em;
  font-size: 1.3em;
  font-weight: bold;
}

@media (max-width: 768px) {
  .container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  header {
    padding: 0 15px;
    display: -ms-flexbox;
    display: flex;
  }

  header .Logo {
    -ms-flex-pack: baseline;
        justify-content: baseline;
    -ms-flex-positive: 1;
        flex-grow: 1;
  }

  header .Menu {
    display: none;
  }

  header .Menu__mobile {
    display: block;
    background-image: url('./img/background.svg');
    background-repeat: no-repeat;
    width: 102px;
    height: 86px;
  }

  header .Menu__burger {
    background-image: url('./img/burger.svg');
    background-repeat: no-repeat;
    background-size: contain;
    width: 40px;
    height: 40px;
    margin: 16px auto;
  }

  main {
    padding: 0 15px;
  }

  nav {
    display: none;
  }

  aside {
    display: none;
  }
}



