.NavList {
  margin: 0 16px;
  margin-bottom: 64px;
}

.NavList__title {
  font-size: 23px;
  font-weight: bold;
  line-height: 1.22;
  color: #928d8d;
  margin: 24px 0;
}

.NavList__item {
  font-size: 14px;
  line-height: 1.5;
  color: #7c2c2d;
  margin: 16px 0;
}

.NavList__link {
  font-size: 14px;
  font-weight: bold;
  line-height: 1.5;
  color: #7c2c2d;
  text-transform: uppercase;
  text-decoration: none;
}

.NavList.GamesList {
  margin-bottom: 0;
  padding-bottom: 16px;
  border-bottom: solid 1px #d6d5d2;
}

.NavList.GamesList .NavList__title {
  font-size: 11px;
  font-weight: normal;
  line-height: 1.45;
  color: #928d8d;
  text-transform: uppercase;
  margin: 16px 0;
}

.NavList.GamesList .NavList__item {
  font-size: 11px;
  line-height: 1.45;
}

.NavList.GamesList .NavList__link {
  font-size: 11px;
}



