.Popup {
  position: absolute;
  top: 0;
  left: 15px;
  width: calc(100% - 62px);
  height: 100vh;
  z-index: 10;
  background-color: #ebebe6;
  -webkit-box-shadow: 0 4px 8px 0 rgba(51, 33, 34, 0.3);
          box-shadow: 0 4px 8px 0 rgba(51, 33, 34, 0.3);
  padding: 16px;
}

.Popup__close-wrapper {
  width: 100%;
  position: relative;
}

.Popup__close {
  position: absolute;
  right: 0;
  width: 40px;
  height: 40px;
  background-image: url('./img/times.svg');
  background-repeat: no-repeat;
  background-size: contain;
}


