.Article {
  background-color: white;
  padding: 24px;
  border-radius: 8px;
  -webkit-box-shadow: 0 8px 16px 0 rgba(51, 33, 34, 0.1);
          box-shadow: 0 8px 16px 0 rgba(51, 33, 34, 0.1);
  margin-bottom: 16px;
}

.Article__header {
  font-size: 11px;
  display: -ms-flexbox;
  display: flex;
  color: #928d8d;
  margin-bottom: 8px;
}

.Article__user {
  -ms-flex-positive: 1;
      flex-grow: 1;
  color: #7c2c2d;
}

.Article__date {
  margin-right: 32px;
}

.Article__body {
  font-size: 14px;
  line-height: 1.5;
  color: #332122;
  margin: 0;
  margin-bottom: 24px;
}

.Article__body:last-of-type {
  margin: 0;
}

.Article__star {
  background-image: url('./img/star.svg');
  background-repeat: no-repeat;
  text-align: right;
  background-position: right 20px center;
}



